@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Noto+Sans+KR:wght@100;300;400;500;700;900&display=swap');

.container {
  margin: auto;
  max-width: 700px;
  font-family: 'Montserrat', sans-serif;
  font-family: 'Noto Sans KR', sans-serif;
}
.todoBlock {
  padding: 30px;
  margin-top: 50px;
  background: #fff;
  border-radius: 10px;
  box-shadow: -9px 17px 13px rgb(0 0 0 /16%);
}
/* App 9-3 style 수정 */
.title {font-size: 15px; color: #6FA6D4;  font-family: 'Montserrat', sans-serif; letter-spacing: -1px;}
.list {background-color: #fff; border-bottom: 1px solid #e6e6e6;  padding: 13px; display: flex; justify-content: space-between; margin: 5px 0; color:#6d6d6d; }
.list span {flex:1; margin-left: 5px;}
.btn {background: none; border: none;color:#6d6d6d }
form {display: flex; justify-content: space-between; margin-top: 10px;}
form input[type=text] {background: #fff; border: 1px solid #e6e6e6;border-radius: 5px; box-shadow: 0 1px 1px rgb(0 0 0 5%); height: 45px; font-size: 15px; padding: 0 10px; width: 86.5%; box-sizing: border-box; outline: none; font-family: 'Noto Sans KR', sans-serif; letter-spacing: 1px;}
form input[type=submit] {width: 12%;height: 45px;background: #6FA6D4; color: #fff; border-radius: 5px; border: none;font-size: 15px; font-family: 'Noto Sans KR', sans-serif; letter-spacing: 2px; }
input[type=checkbox] {opacity: 0;position: absolute;z-index: 12;width: 20px;height: 20px;  }
input[type=checkbox] + span {position: relative; padding-left: 30px; }
input[type=checkbox] + span:before {content: '';position: absolute;left: 0; top: 50%; transform: translateY(-50%); width: 20px; height: 20px; line-height: 20px;border-radius: 3px; background: #fff; border: 1px solid #e6e6e6; background: #F3F0DF;}
input[type=checkbox]:checked + span:before { color: #fff;content: '✔'; color: #A67B59; text-align: center; font-size: 20px;}
/* 체크표시 보통 이미지나 웹폰트 많이 쓰지만 그냥 기호 복사해서 썼음 -> https://unicode-table.com/kr/search/?q=checkbox */
/* 할일 리스트 completed true 일때 span.completed */
.completed {text-decoration: line-through; color: #999;}
/* App 10. 모두 삭제 버튼 추가 */
.title {display: flex; justify-content: space-between;}
.deleteBtn {background: none; font-weight: bold; color: #6FA6D4; border: none; font-size: 16px; cursor: pointer; font-family: 'Montserrat', sans-serif; letter-spacing: -1px;}